<template>
  <v-chip
    small
    :color="color"
    outlined
    class="text-uppercase"
    style="
      border: none;
      background-color: #e9e9e9 !important;
      border-radius: unset;
      padding: 0 7px;
    "
    >{{ label }}</v-chip
  >
</template>

<script>
import colors from "vuetify/lib/util/colors";
import SocketHolder from "../common/SocketHolder";

export default {
  name: "Status",

  props: {
    value: Boolean,
    koLabel: String,
    okLabel: String,
  },

  computed: {
    label() {
      return this.value
        ? `${this.okLabel} - ${
            SocketHolder.socket ? SocketHolder.socket?.id : null
          }`
        : this.koLabel;
    },
    color() {
      return this.value ? colors.green.base : colors.red.base;
    },
  },
};
</script>
