<template>
  <v-app>
    <AppBar @update="showConnectionModal = true" />

    <!-- <NavigationDrawer />

    <v-main :class="backgroundColor">
      <v-container fluid>
        <component :is="transitionName" hide-on-leave>
          <router-view />
        </component>
      </v-container>
    </v-main> -->

    <ConnectionModal
      :is-open="showConnectionModal"
      :initial-server-url="serverUrl"
      :initial-ws-only="wsOnly"
      :initial-path="path"
      :initial-params="params"
      :is-connecting="isConnecting"
      :error="connectionError"
      @submit="onSubmit"
    />

    <!-- <LoginModal
      :is-open="false"
      :is-login="false"
      :error="connectionError"
      @submit="onSubmitLogin"
    /> -->
  </v-app>
</template>

<script>
import AppBar from "./components/AppBar";
// import NavigationDrawer from "./components/NavigationDrawer";
import { io } from "socket.io-client";
import ConnectionModal from "./components/ConnectionModal";
// import LoginModal from "./components/LoginModal";
import SocketHolder from "./common/SocketHolder";
import { mapState } from "vuex";
// import {
//   VSlideXTransition,
//   VSlideXReverseTransition,
//   VSlideYTransition,
//   VSlideYReverseTransition,
// } from "vuetify/lib";

export default {
  name: "App",

  components: {
    // LoginModal,
    ConnectionModal,
    // NavigationDrawer,
    AppBar,
    // VSlideXTransition,
    // VSlideXReverseTransition,
    // VSlideYTransition,
    // VSlideYReverseTransition,
  },

  data: () => ({
    showConnectionModal: false,
    isConnecting: false,
    connectionError: "",
    transitionName: "v-slide-x-reverse-transition",
  }),

  computed: {
    ...mapState({
      serverUrl: (state) => state.connection.serverUrl,
      wsOnly: (state) => state.connection.wsOnly,
      path: (state) => state.connection.path,
      params: (state) => state.connection.params,
      backgroundColor: () => "grey lighten-5",
    }),
  },

  watch: {
    $route(to, from) {
      if (to.meta.topLevel && from.meta.topLevel) {
        this.transitionName =
          to.meta.index > from.meta.index
            ? "v-slide-y-reverse-transition"
            : "v-slide-y-transition";
      } else {
        this.transitionName = to.meta.topLevel
          ? "v-slide-x-transition"
          : "v-slide-x-reverse-transition";
      }
    },
  },

  methods: {
    tryConnect(serverUrl, wsOnly, path, params) {
      this.SocketSignalCodes = {
        // Server To Clients
        SIGNAL_KICK: 100,
        SIGNAL_CLIENT_ONLINE: 101,
        SIGNAL_CLIENT_OFFLINE: 102,
        SIGNAL_PROFILE: 103,
        SIGNAL_BANNED: 104,
        SIGNAL_CREATED_ROOM: 105,
        SIGNAL_CREATED_ROOM_MESSAGE: 106,
        SIGNAL_DELETED_ROOM: 107,
        SIGNAL_DELETED_ROOM_MESSAGE: 108,
        SIGNAL_TYPING_ROOM_MESSAGE: 109,

        // Clients to Server
        // For Emit
        CLIENT_SIGNAL_SET_MESSAGE_RECIPIENT_TYPE: 201,
        CLIENT_SIGNAL_TYPING_MESSAGE: 202,
      };
      this.isConnecting = true;
      if (SocketHolder.socket) {
        SocketHolder.socket.disconnect();
        SocketHolder.socket.off("connect");
        SocketHolder.socket.off("connect_error");
        SocketHolder.socket.off("disconnect");
        SocketHolder.socket.off("_signal");
      }
      SocketHolder.socket = io(serverUrl, {
        forceNew: true,
        reconnection: false,
        transports: wsOnly ? ["websocket"] : ["polling", "websocket"],
        path: `${path}`,
        query: (params || "")
          .substr(1)
          .split("&")
          .reduce(function (map, obj) {
            map[obj.split("=")[0]] = obj.split("=")[1];
            return map;
          }, {}),
      });
      SocketHolder.socket.once("connect", () => {
        this.showConnectionModal = false;
        this.connectionError = "";
        this.isConnecting = false;

        SocketHolder.socket.emit("_signal", 10, "123");

        SocketHolder.socket.io.reconnection(true);
        this.$store.commit("connection/saveConfig", {
          serverUrl,
          wsOnly,
          path,
          params,
        });
      });
      SocketHolder.socket.on("connect", () => {
        this.$store.commit("connection/connect");
      });
      SocketHolder.socket.on("connect_error", (err) => {
        if (this.isConnecting || err.message === "invalid credentials") {
          this.showConnectionModal = true;
          this.connectionError = err.message;
          this.isConnecting = false;
        }
      });
      SocketHolder.socket.on("disconnect", () => {
        this.$store.commit("connection/disconnect");
      });
      SocketHolder.socket.on("_signal", (code, data) => {
        if (code === this.SocketSignalCodes.SIGNAL_KICK) {
          this.showConnectionModal = true;
          this.connectionError = `Kicked - ${data}`;
          this.isConnecting = false;
          SocketHolder.socket.disconnect();
        }
      });
    },
    onSubmit(form) {
      this.tryConnect(form.serverUrl, form.wsOnly, form.path, form.params);
    },
    onSubmitLogin(form) {
      console.log(form);
    },
  },

  created() {
    if (this.serverUrl) {
      this.tryConnect(this.serverUrl, this.wsOnly, this.path, this.params);
    } else {
      this.showConnectionModal = true;
    }
  },
};
</script>
