<template>
  <v-dialog
    :value="isOpen"
    transition="dialog-bottom-transition"
    max-width="600"
    persistent
  >
    <v-card>
      <v-card-title>{{ $t("connection.title") }}</v-card-title>
      <v-card-text>
        <form @submit.prevent="onSubmit">
          <v-text-field
            v-model="serverUrl"
            :label="$t('connection.serverUrl')"
            placeholder="https://example.com, wss://example.com"
            required
          ></v-text-field>

          <v-text-field
            v-model="path"
            :label="$t('connection.path')"
          ></v-text-field>

          <v-text-field
            v-model="params"
            :label="$t('connection.params')"
            placeholder="?key1=val1&key2=val2..."
          ></v-text-field>

          <v-row>
            <v-col class="d-inline-flex">
              <v-switch
                style="width: 100%; margin-top: 7px"
                v-model="wsOnly"
                :label="$t('connection.websocket-only')"
                inset
                dense
              />

              <v-btn
                :loading="isConnecting"
                :disabled="isConnecting || !isValid"
                type="submit"
                class="primary"
                >{{ $t("connection.connect") }}
              </v-btn>
            </v-col></v-row
          >

          <div v-if="error" class="red--text mt-3">
            {{ errorMessage }}
          </div>
        </form>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ConnectionModal",

  props: {
    isOpen: Boolean,
    isConnecting: Boolean,
    initialServerUrl: String,
    initialWsOnly: Boolean,
    initialPath: String,
    initialParams: String,
    error: String,
  },

  data() {
    return {
      serverUrl: this.initialServerUrl,
      wsOnly: this.initialWsOnly,
      path: this.initialPath,
      params: this.initialParams,
    };
  },

  computed: {
    isValid() {
      return this.serverUrl && this.serverUrl.length;
    },
    errorMessage() {
      return this.error === "invalid credentials"
        ? this.$t("connection.invalid-credentials")
        : this.$t("connection.error") + this.$t("separator") + this.error;
    },
  },

  methods: {
    onSubmit() {
      this.$emit("submit", {
        serverUrl: this.serverUrl,
        wsOnly: this.wsOnly,
        path: this.path,
        params: this.params,
      });
    },
  },
};
</script>

<style scoped></style>
