<template>
  <v-app-bar app clipped-left>
    <img :src="logoSrc" alt="logo" max-height="20" max-width="20" style="height: 40px" />
    <v-toolbar-title class="pl-3">Experimental</v-toolbar-title>
    <v-btn small class="pa-0 ml-2 elevation-0 pt-1" :href="linkToReleaseNotes">{{
      version
    }}</v-btn>

    <v-spacer />

    <div class="d-flex">
      <div>
        <div style="float: right">
          <code v-if="serverUrl"
            >{{ $t("connection.serverUrl") }}{{ $t("separator")
            }}{{ serverUrl }}</code
          >
        </div>
        <div>
          <ConnectionStatus :connected="connected" />
        </div>
      </div>

      <v-btn @click="onUpdate" class="ml-3 align-self-center">{{
        $t("update")
      }}</v-btn>
    </div>
  </v-app-bar>
</template>

<script>
import { mapState } from "vuex";
import ConnectionStatus from "./ConnectionStatus";

const version = "0.0.1";

export default {
  name: "AppBar",

  components: { ConnectionStatus },

  data() {
    return {
      version,
    };
  },

  computed: {
    ...mapState({
      logoSrc: () => "/favicon.png",
      serverUrl: (state) => state.connection.serverUrl,
      connected: (state) => state.connection.connected,
    }),
    linkToReleaseNotes() {
      return "";
    },
  },

  methods: {
    onUpdate() {
      this.$emit("update");
    },
  },
};
</script>
