import { isLocalStorageAvailable } from "../../common/util";

export default {
  namespaced: true,
  state: {
    serverUrl: location.origin,
    wsOnly: false,
    path: "/experimental",
    params: "?ex-authorization=TOKEN&ex-language=ru",
    connected: false,
    sid: null,
  },
  mutations: {
    init(state) {
      if (isLocalStorageAvailable) {
        state.serverUrl = localStorage.getItem("server_url") || location.origin;
        state.wsOnly = localStorage.getItem("ws_only") === "true";
        state.path = localStorage.getItem("path") || "/experimental";
        state.params =
          localStorage.getItem("params") ||
          "?ex-authorization=TOKEN&ex-language=ru";
      }
    },
    saveConfig(state, { serverUrl, wsOnly, path, params }) {
      state.serverUrl = serverUrl;
      state.wsOnly = wsOnly;
      state.path = path;
      state.params = params;
      if (isLocalStorageAvailable) {
        localStorage.setItem("server_url", serverUrl);
        localStorage.setItem("ws_only", wsOnly);
        localStorage.setItem("path", path);
        localStorage.setItem("params", params);
      }
    },
    connect(state) {
      state.connected = true;
    },
    disconnect(state) {
      state.connected = false;
    },
  },
};
