import { isLocalStorageAvailable } from "../../common/util";

export default {
  namespaced: true,
  state: {
    lang: "en",
  },
  mutations: {
    init(state) {
      if (isLocalStorageAvailable) {
        state.lang = localStorage.getItem("lang") || "en";
      }
    },
    selectLang(state, lang) {
      state.lang = lang;
      if (isLocalStorageAvailable) {
        localStorage.setItem("lang", lang);
      }
    },
  },
};
